import btnComponent from '@/components/btnComponent.vue';

export default {
	components: {
		btnComponent
	},
	data() {
		return {
			userInfo: {}
		};
	},
	onLoad() {
		if (!this.userInfo.nickname) {
			let timeId = setInterval(() => {
				if (this.$app.getData('userInfo')) {
					clearInterval(timeId)
					this.userInfo = this.$app.getData('userInfo')
				}
			}, 300)
		}
	},

}
